monaco-editor {
  display: block;
  width: 100%;
  min-height: 40px;
  margin: 20px 0;
}

.monaco-editor {
  padding: 12px 0;
}

.palette-color-block {
  padding: 9px 26px;
  font-size: 12px;
  color: #2d2d2d;
  width: 100px;
  text-align: center;
  font-weight: bold;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.palette-title {
  padding-left: 4px;
  font-size: 18px;
  margin-top: 12px;
}

.palettes-container > div {
  padding: 12px;
}

.article-title {
  font-size: 60px;
  font-weight: 400;
  line-height: 1.2;
  margin: 0 0 16px;
}

.article-subtitle {
  font-size: 20px;
  line-height: 1.6;
  font-weight: 400;
  margin: 0 0 8px;
}

section h2 {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
  margin: 0 0 8px;
}

section h2:not(:first-child) {
  margin-top: 24px;
}

section p {
  margin: 0 0 16px;
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.eyebrow {
  /* color: #5f6368; */
  color: var(--mdw-theme-text--on-background);
  display: block;
  line-height: 20px;
  margin: 24px 0 4px;
}

article > section {
  padding-top: 56px;
}

header mdw-top-app-bar {
  background-color: #332f36;
  color: #8d7bc7;
  border-left: 1px solid rgba(255,255,255,0.12);
}

mdw-sheet-side.navigation mdw-header {
  background-color: #332f36;
}

.navigation {
  background-color: #332f36 !important;
}

.navigation .main-title {
  color: #8d7bc7;
  display: block;
  font-size: 24px;
  /* padding-left: 24px; */
  line-height: 25px;
  font-weight: 500;
}
.navigation .main-title:first-child {
  margin-top: 16px;
}

nav {
  padding-top: 24px;
}

nav .title {
  color: #76a5aa;
  display: block;
  font-size: 18px;
  padding-bottom: 12px;
  padding-top: 24px;
  margin-left: 16px;
}

nav mdw-divider {
  margin-top: 24px;
}

.links {
  display: inline-grid;
}

anchor-link {
  display: block;
  font-family: Roboto,Arial,Helvetica,sans-serif;
  font-size: 1rem;
  line-height: 1.6rem;
  letter-spacing: .2px;
  font-weight: 500;
  text-decoration: none;
  font-weight: 500;
  color: var(--mdw-theme-primary);
  cursor: pointer;
}

mdw-sheet-side.mdw-navigation-drawer nav a {
  color: #b3b3b3;
}

li a {
  display: inline-block;
  font-size: 1.1rem;
  font-weight: lighter;
  color: #7499cb;
  cursor: pointer;
  text-decoration: none;
}

.page-article {
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 56px;
}

.into-article {
  padding: 24px;
}

.sub-article {
  padding: 24px;
}

.column-article {
  padding: 12px;
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.spacer {
  padding-top: 36px;
}

flex {
  display: flex;
  flex: 1;
}

.disclaimer-container {
  margin: 1.5em 0;
  padding: 8px 16px;
  overflow: hidden;
  border-left: 4px solid #ffab40;
  background-color: #ffcdd2;
  border-left-color: #ff5252;
}

.direction {
  font-size: 0.875rem;
  margin: 1.5em 0;
  padding: 8px 16px;
  overflow: hidden;
  background-color: #EEE;
}

.CodeMirror {
  border: 1px solid #eee;
  height: auto !important;
}

.showcase {
  min-height: 200px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 24px;
  margin: 24px 0;
}

.showcase label {
  /* color: var(--mdw-theme-text--on-primary); */
}

