/* --- codecase: for displaying demoo next too code --- */

.codecase {
  width: 100%;
  padding: 12px 0;
  margin: 36px 0;
  box-shadow: 0 3px 1px -2px rgba(0,0,0,.2),
              0 2px 2px 0 rgba(0,0,0,.14),
              0 1px 5px 0 rgba(0,0,0,.12);
}

.codecase .title {
  /* color: #202124; */
  font-weight: 400;
  font-size: 1.25rem;
  letter-spacing: normal;
  padding-left: 20px;
  line-height: 46px;
}

.codecase .description {
  /* color: #202124; */
  font-weight: 400;
  margin: 0 0 8px;
  font-size: 1rem;
  letter-spacing: normal;
  padding-left: 20px;
}

.codecase a {
  padding-left: 20px;
  margin: 0;
}

.codecase pre {
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
}

.codecase .demo {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  padding: 8px;
}

.mdw-is-mobile code-mirror {
  display: inline;
}

code-mirror code {
  left: -80px;
  position: relative;
}

section mdw-card code-mirror code {
  left: -160px;
  position: relative;
}

mdw-card code-mirror code {
  left: -120px;
  position: relative;
}



a.mdw-current-link {
  color: #e3fdff !important;
}

a.mdw-current-link:not([disabled]):after,
a.mdw-current-link:not([disabled]):before {
  background-color: #968b9e !important;
}
